body {
    margin: 0;
    padding: 0;
    font-family: "Noto Sans KR",sans-serif;
    background-color: #F1E8F1;
}

.add_con{
   padding: 50px;
} 
.add_box{max-width: 450px; margin: 0 auto;background: #fff;box-shadow: 9px 15px 10px rgba(72, 46, 46, 0.25);border-radius: 20px;
padding: 20px;
}
.title{
    padding-top: 20px; padding-bottom: 20px;
    color: #8499c7;
    font: italic 700 35px "Roboto Condensed";
    letter-spacing: -.5px;
}

.contents{
    border-top: 2px solid #a9a7a763;
    min-height: 320px;
    padding: 40px 30px 20px 30px;
    box-sizing: border-box;
}
.list_box{
    display: flex;
    align-items: center;
    padding: 13px 0;
    border-bottom: 1px solid #eee;
    color: #333;
    font-size: 20px;
    justify-content: space-between;
}
.list_box a {width: 100%;text-align: left;
text-decoration: none;color: #4a2e2e;}
.checkbox{display: flex;
    flex: 1 1;
    align-items: center;
    cursor: pointer;}

.checkbox .text {
        margin-left: 12px;
    }
.add_list{

    width: 100%;
   
}
.add_list button{
    padding-right: 0;
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
}
.add_list button svg ,.update,.back{
    color: #50e3a4;
    font-size: 60px;
    vertical-align: middle;
}
.add_list div{
    padding-right: 0;
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
    display: inline-block;
}
.add_list div svg {
    color: #50e3a4;
    font-size: 60px;
    vertical-align: middle;
}
.add_list input{
    line-height: 1.5;
    outline: none;
    border: none;
    border-bottom: 1px solid #aaa;
    color: #333;
    font-size: 20px;
    width: 100%;
    box-sizing: border-box;
}
.remove {
    margin-left: 5px;
    font-size: 25px;
    cursor: pointer;
}
.checkbox.checked svg {
    color: #50e3a4;
}
.update_con {justify-content: space-between;display: flex;}

.hide-enter {opacity: 1;}

.hide-enter-active {opacity: 0;
transition: 0.5s;
}

